<template>
  <div class="medicinerecordprintmanage">
    <v-row>
      <v-col cols="12" md="6">
        <h2 class="subheading grey--text">
          <v-icon class="mx-1">mdi-pill</v-icon>
          <span>給藥紀錄單列印管理</span>
        </h2>
      </v-col>
      <v-col cols="12" md="6" class="text-right">
        <v-btn class="mx-1 primary" @click.stop="goDocument">回就醫及藥品紀錄維護頁面</v-btn>
      </v-col>
    </v-row>

    <v-card>
      <v-card-title>
        <v-spacer></v-spacer>
      </v-card-title>

      <v-card-text>
        <v-card>
          <v-card-title class="primary lighten-2 white--text">
            病歷資料列表 已選擇: {{checkedfilter.length}}
            <v-btn class="primary ml-3" @click="selectOnBed">有床號</v-btn>
            <v-btn class="primary mx-1" @click="selectAll">全選</v-btn>
            <v-btn color="primary" @click="selectCancelAll">全不選</v-btn>
            <v-spacer></v-spacer>

            <v-text-field label="列印月份設定" type="month" v-model="printMonth" hide-details></v-text-field>
            <v-btn class="error ml-2" @click.stop="printConfirm" v-show="checkedfilter.length > 0">
              <v-icon class="mr-1" small>mdi-pill</v-icon>列印藥單
            </v-btn>
          </v-card-title>
          <v-card-text class="mt-2">
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="serachKey"
              ></v-text-field>
            </v-toolbar>

            <div style="overflow:auto">
              <v-simple-table fixed-header class="text-no-wrap mt-2" height="60vh">
                <template v-slot:default>
                  <thead style="background-color: #e3f2fd;">
                    <tr>
                      <th>選取</th>
                      <th>病歷號</th>
                      <th>姓名</th>
                      <th>床號</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item,index) in searchfilter" :key="index">
                      <td>
                        <v-checkbox v-model="item.check"></v-checkbox>
                      </td>
                      <td>{{item.p_code}}</td>
                      <td>{{item.name}}</td>
                      <td>{{item.bed_name}}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </div>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>
    <!-- <pre> {{selectUser}} </pre> -->
  </div>
</template>

<script>
import qs from "qs";
import feelist from "@/components/FeeList";
import dayjs from "dayjs";

export default {
  components: { feelist },
  data() {
    return {
      items: [], //病歷資料
      medicalRecordItems: [],
      eatMedicineTimeItems: [],
      serachKey: "", //病歷搜尋欄
      model: "", //list初選值
      selectUser: [], //目前選擇病歷
      printMonth: dayjs().format("YYYY-MM"), //列印月份
      // nowday: dayjs().format("YYYY-MM-DD"), //日期暫存
      // nowmonth: dayjs().format("YYYY-MM"), //日期暫存
    };
  },

  computed: {
    searchfilter() {
      var keys = this.serachKey.split(" ");
      var str = "";

      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
    },
    checkedfilter() {
      //取出己選取的評估單
      return this.items.filter((item) => {
        return item.check == true;
      });
    },
  },

  mounted() {
    // this.auth = this.$store.state.pData;
    this.getAllData();
    this.getAllMedical();
    this.getEatMedicineTime();
  },

  methods: {
    selectOnBed() {
      this.items.filter((item) => {
        if (item.bed_name != null) {
          item.check = true;
        }
      });
    },
    selectAll() {
      this.items.filter((item) => {
        item.check = true;
      });
    },
    selectCancelAll() {
      this.items.filter((item) => {
        item.check = "";
      });
    },
    goDocument() {
      this.$router.push("/main/document/medicinerecordlist");
    },
    sortBybedname(items) {
      //病歷依床號排序
      return items.sort(function (a, b) {
        return a.bed_name > b.bed_name ? 1 : -1;
      });
    },
    getAllData() {
      // console.log("取得所有病歷資料");
      //取得所有病歷資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.axios
        .post("user/getAll/" + this.$store.state.databaseName)
        .then((rs) => {
          this.items = rs.data.map((i) => {
            i.check = "";
            return i;
          });
          this.items = this.sortBybedname(this.items);
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    getAllMedical() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post("medicinerecord/getallmedical/" + this.$store.state.databaseName)
        .then((rs) => {
          // console.log(rs.data)
          this.medicalRecordItems = rs.data.map((i, index) => {
            if (i["pipeline"].length > 0) {
              i["pipeline"] = JSON.parse(i.pipeline);
            } else {
              i["pipeline"] = [];
            }
            return i;
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    //取得所有吃藥時間
    getEatMedicineTime() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getAll/" +
            this.$store.state.databaseName +
            "/eat_medicine_time"
        )
        .then((rs) => {
          this.eatMedicineTimeItems = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    printConfirm() {
      //判斷確認列印
      this.$confirm("確認顯示列印畫面?").then((res) => {
        if (res) {
          var matchUsers = this.items.filter((i) => {
            return i.check;
          });

          this.axios
            .post(
              "medicinerecord/getallmedicine/" + this.$store.state.databaseName
            )
            .then((rs) => {
              var medicineItems = rs.data; //所有藥物資料

              var matchData = matchUsers.map((user) => {
                var medicineData = medicineItems.filter((mitem) => {
                  if (
                    mitem.name != undefined &&
                    user.snkey == mitem.user_snkey &&
                    mitem.stop != "true" &&
                    dayjs(this.printMonth).format("YYYY-MM") >=
                      dayjs(mitem.start_date).format("YYYY-MM") &&
                    dayjs(this.printMonth).format("YYYY-MM") <=
                      dayjs(mitem.medicineEndInfo.substring(3, 13)).format(
                        "YYYY-MM"
                      )
                  ) {
                    mitem["end_date"] = mitem.medicineEndInfo.substring(3, 13);
                    return mitem;
                  }
                });

                //取得各藥物的就診紀錄
                // medicineData = medicineData.filter((i) => {
                //   return this.medicalRecordItems.filter((j) => {
                //     if (i.medical_snkey == j.snkey) {
                //       i["medical"] = j;
                //       return i;
                //     }
                //   });
                // });

                //取得各藥物的就診紀錄
                medicineData = medicineData.filter((i) => {
                  var matchmedicalRecord = this.medicalRecordItems.find((j) => {
                    return i.medical_snkey == j.snkey;
                  });

                  //不存在的就診資料不會被列入,不然列印時會出錯
                  if (matchmedicalRecord) {
                    i["medical"] = matchmedicalRecord;
                    return i;
                  }
                });

                //取得各藥物的吃藥時間紀錄
                medicineData = medicineData.filter((i) => {
                  var matcheatMedicineTime = this.eatMedicineTimeItems.find((j) => {
                    return i.eat_medicine_time_snkey == j.snkey;
                  });

                  if (matcheatMedicineTime) {
                    i["eat_medicine_time"] = matcheatMedicineTime;
                    return i;
                  }
                });
                // medicineData = medicineData.filter((i) => {
                //   return this.eatMedicineTimeItems.filter((j) => {
                //     if (i.eat_medicine_time_snkey == j.snkey) {
                //       i["eat_medicine_time"] = j;
                //       return i;
                //     }
                //   });
                // });

                return {
                  user: user,
                  medicineData: medicineData,
                };
              });

              //去掉沒有資料的
              matchData = matchData.filter((i) => {
                return i.medicineData.length > 0;
              });

              var printData = {
                printMonth: this.printMonth,
                matchData: matchData,
              };

              //check錯誤用
              // printData.matchData.filter((item,index)=>{
              //   console.log('data:'+index)

              //   item.medicineData.filter((mitem,index)=>{
              //     console.log(mitem)
              //     // if (mitem.eat_medicine_time.time == ""){
              //     //   console.log(index,mitem)
              //     // }
              //   })
              // })
              // return false
              //check錯誤用end

              sessionStorage.setItem("printData", JSON.stringify(printData));
              let routeData = this.$router.resolve({
                path: "/print/multimedicineprint",
              });
              window.open(routeData.href, "_blank");
            });
        }
      });
    },
  },
};
</script>


<style lang="scss">
.medicinerecordprintmanage {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1rem;
      font-family: "微軟正黑體";
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>